.story-container {
    padding: 0;
    margin: 0;
    list-style: none;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    align-items: center;
    -webkit-flex-flow: row nowrap;
    justify-content: flex-start;
    padding: 0px 0px 10px 10px;
}

.story-item {
    margin: 2px;
}

.story-text {
    margin-left: 5px;
}